<template>
  <div id=root v-if="words.length > 0">
    <game-wrap :words="this.words"></game-wrap>
  </div>
</template>

<script>
  import GameWrap from './components/GameWrap.vue';

  export default {
    name: 'app',
    components: {GameWrap},
    props: {
      words: Array,
    }
  }
</script>

<style>
  #root {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    height: 100%;
  }
</style>



