<template>
  <div class="spell-check-result mt-2">
    <p>
      You wrote "{{result.typedWord}}"
      <template v-if="result.correct"> correctly</template>
      <template v-else-if="result.forgot"> and forgot the word "{{result.word}}" </template>
      <template v-else> instead of the "{{result.word}}"</template>
      in {{result.totalTime}} secs, from which you were watching the word during {{result.watchTime}} secs,
      waiting until typing during {{result.startTypeTime}} secs
      and typing for {{result.typingTime}} secs
    </p>
    <div class="divider"></div>
  </div>
</template>

<script>
  /*
  Recieves an object result with such properties:
  - correct (whether the word was typed correctly)
  - typedWord (the word that was typed by a child)
  - word (word that was shown to the child)
  - watchTime (time during which a child was looking at the shown word)
  - startTypeTime (time that passed to the moment he inputted the first letter)
  - typingTime (time that took a child to write the whole word)
  - totalTime (the whole time)
   */

  export default {

    props: {
      result: {
        type: Object,
        required: true,
      }
    },

  }
</script>



