<template>
  <div>
    <div v-show="stage===1" class="gameContent">
      <p class="word">{{word}}</p>
      <button type="button" @click="toSecondStage">I`m ready to write</button>
    </div>


    <div v-show="stage===2" class="gameContent">
      <p>Write the word bellow </p>
      <div id="spell-check-stage-2">
        <input type="text" id="spell-check-input-word" ref="inputWord" @input="handleInput"
               :class="'input-word-'+word.length"
               :data-word-length="word.length"
               :maxlength="word.length"
               v-model="typedWord"
               @keydown.tab.prevent="">
        <div id="border-word" :class="'input-word-'+word.length"></div>
      </div>
      <button type="button" @click="forgotWord">Can`t recall</button>
    </div>


    <div v-show="stage===3" @keydown.enter="endCheckGame" class="gameContent">
      <p class="word success" v-if="typedWord.length > 0 && correct">{{typedWord}}</p>
      <p class="word unsuccess" v-else-if="typedWord.length >0" v-html="getIncorrectLetters()"></p>
      <p class="word gray" v-else >no word</p>

      <button type="button" @click="endCheckGame">{{last ? "End game" : "Next word"}}</button>
    </div>
  </div>
</template>

<script>

  /*
  A game that consist of 3 stages:
  - show a word to a child and record time the child was looking at the word
  (a child can come come to the next stage by pressing space button or button "I'm ready to write"
  - show an input field (that consists of component letters) of the word length
  (the child can move to the next stage when he/she hit the button "Can't recall" or inputs the whole word)
  - show mistakes that were made or the congratulations with a correct typing"
  (the child can move to the result by pressing enter ot button "Back to results")

  component handle changing input position and doesn't provide a chance to start typing in a wrong place
   */


  export default {
    props: {
      word: {
        type: String,
        required: true
      },
      last: {
        type: Boolean,
        required: false
      }

    },
    data() {
      return {
        stage: 1,
        watchTime: 0,
        startTypeTime: 0,
        typingTime: 0,
        startedTyping: false,
        currentLetter: 0,
        startTime: new Date(),
        typedWord: "",
        forgot: false,
      }
    },
    mounted() {
      document.addEventListener("keydown", this.keyHit);
    },
    destroyed() {
      document.removeEventListener("keydown", this.keyHit)
    },
    computed: {
      wordArray() {
        return this.word.split('')
      },
      correct() {
        return this.typedWord === this.word
      },
    },
    methods: {
      keyHit(e) {
        if (e.code === 'Enter' && this.stage === 1) {
          this.toSecondStage()
        }
        else if (e.code === 'Enter' && this.stage === 2) {
          if (this.word.length === this.typedWord.length) {
            this.toThirdStage();
          }
          else {
            this.forgotWord();
          }
        }
        else if (e.code === 'Enter' && this.stage === 3) {
          this.endCheckGame();
        }
      },
      registerWatchTime() {
        this.watchTime = new Date() - this.startTime;
      },
      toSecondStage() {
        this.registerWatchTime();
        this.stage++;
        setTimeout(this.focusInput, 1)
      },
      focusInput() {
        this.$refs.inputWord.focus()
      },
      registerStartTypeTime() {
        this.startTypeTime = new Date() - this.startTime - this.watchTime;
      },
      registerTypingTime() {
        this.typingTime = this.startTypeTime ? new Date() - this.startTime - this.watchTime - this.startTypeTime : 0;
      },
      handleInput() {
        this.typedWord = this.typedWord.trim();
        if (this.typedWord.length === 1 && !this.startedTyping) {
          this.startedTyping = true;
          this.registerStartTypeTime();
        }
        if (this.typedWord.length === this.word.length) {
          this.registerTypingTime();
          this.toThirdStage();
        }
      },
      forgotWord() {
        this.forgot = true;
        this.toThirdStage();
      },
      toThirdStage() {
        this.stage++;
      },
      getIncorrectLetters() {
        let incorrect = '';
        for (let i = 0; i < this.typedWord.length; i++) {
          if (this.typedWord[i] !== this.word[i]) {
            incorrect += '<span class="wrong-letter">' + this.typedWord[i] + '</span>'
          }
          else {
            incorrect += this.typedWord[i]
          }
        }
        return incorrect
      },
      endCheckGame() {
        this.$emit('endGame', this.watchTime, this.startTypeTime, this.typingTime, this.correct, this.word, this.typedWord, this.forgot);
        Object.assign(this.$data, this.$options.data());

      },
    },
  }
</script>

<style lang="scss" scoped>
  .gameContent {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .wrong-letter {
    color: red;
  }

  .word {
    font-family: Courier;
    letter-spacing: 40px;
    font-size: 70px;
    margin-bottom: 0;
    margin-left: 40px;
    &.gray{
      font-size: 50px;
      color: gray;
    }
  }

  .success {
    color: green;
  }

  button {
    display: flex;
    padding: 20px 50px 20px 50px;
    font-size: 20px;
    margin: 10px;
    border: 2px solid #38B5ED;
    border-radius: 10px;
    &:hover {
      background-color: dodgerblue;
      border: 2px solid dodgerblue;
      color: white;
    }
  }

  #spell-check-stage-2 {
    margin-left: 40px;
    #spell-check-input-word {
      font-family: Courier;
      font-size: 100px;
      text-align: left;
      letter-spacing: 40px;
      content: attr(data-word-length);
      /*margin-left: 30px;*/
      /*margin-right: 30px;*/
      border: transparent;
      outline: transparent !important;

    }

    #border-word {
      height: 4px;
      background: repeating-linear-gradient(to right,
        gray 0px,
        gray 60px,
        transparent 60px,
        transparent 100px);
    }
    $min_word: 2;
    $max_word: 30;
    @for $i from $min_word through $max_word {
      .input-word-#{$i} {
        width: 60px * $i + 40px * ($i - 1) + 40px;
        height: 100px;
      }
    }

  }


</style>



