<template>
  <div>
    <div v-if="gameStart">
      <spell-check-game :word="word" :last="wordInRound===words.length-1" @endGame="saveResults"></spell-check-game>
    </div>
    <template v-else>
      <template v-if="!!results.length">
        <div class="resuls" id="results"
             :data-total-words="results.length"
             :data-correct-words="correctWords"
             :data-forgotten-words="totalForgot"
             :data-average-watching-time="averageWatchingTime"
             :data-average-start-type-time="averageStartTypeTime"
             :data-average-typing-time="averageTypingTime"
        >
          <h1> Results of the round: </h1>
          <div class="row">
            <div class="col-4">Total time: {{totalTime}}</div>
            <div class="col-4" id="correct-words">Correct words: {{correctWords}} out of {{results.length}}</div>
            <div class="col-4" id="forgotten-words">Forgotten words: {{totalForgot}} out of {{results.length}}</div>
            <div class="col-4" id="average-watching-time">Average watching time: {{averageWatchingTime || 0 }} sec</div>
            <div class="col-4" id="average-start-type-time">Average start type time: {{averageStartTypeTime || 0}} sec
            </div>
            <div class="col-4" id="average-typing-time">Average typing time: {{averageTypingTime || 0}} sec</div>
          </div>
          <div class="divider"></div>
          <spell-check-results v-for="(res, key) in results" :key="key" :result="res">
          </spell-check-results>
          <div class="row justify-content-center ">
            <button id="save-results" class="mbtn" ref="saveGameResults">Save results</button>
          </div>
        </div>
      </template>
      <button v-if="!results.length" id="start-game" class="mbtn" @click="startGame" type="button"
              style="margin-top:100%">I'm ready to start
      </button>
    </template>
  </div>
</template>

<script>
  import SpellCheckResults from './SpellCheckResults.vue'
  import SpellCheckGame from './SpellCheckGame.vue'
  /*
  The component is a block to unite results of the game and the game
  sends a word to the game
  send the results of the game to the resulst
   */
  export default {
    components: {SpellCheckResults, SpellCheckGame},
    props: {
      words: {
        type: Array,
        required: true,
        validator: function (value) {
          return value.length > 0
        }
      }
    },
    data() {
      return {
        round: 0,
        wordInRound: 0,
        wordInd: 0,
        word: this.words[0],
        gameStart: false,
        results: []
      }

    },
    mounted() {
      document.addEventListener("keydown", this.keyHit);
    },
    destroyed() {
      document.removeEventListener("keydown", this.keyHit);
    },
    computed: {
      totalTime() {
        let total = 0;
        for (let i = 0; i < this.results.length; i++) {
          total += this.results[i].totalTime;
        }
        return total;
      },
      correctWords() {
        let correct = 0;
        for (let i = 0; i < this.results.length; i++) {
          correct += this.results[i].correct;
        }
        return correct;
      },
      totalForgot() {
        let forgot = 0;
        for (let i = 0; i < this.results.length; i++) {
          forgot += this.results[i].forgot;
        }
        return forgot;
      },
      averageWatchingTime() {
        let watchingTime = 0;
        let totalWord = 0;
        for (let i = 0; i < this.results.length; i++) {
          if (!this.results[i].forgot) {
            watchingTime += this.results[i].watchTime;
            totalWord += 1
          }
        }
        return watchingTime / totalWord
      },
      averageStartTypeTime() {
        let startTypeTime = 0;
        let totalWord = 0;
        for (let i = 0; i < this.results.length; i++) {
          if (!this.results[i].forgot) {
            startTypeTime += this.results[i].startTypeTime;
            totalWord += 1
          }
        }
        return (startTypeTime / totalWord)
      },
      averageTypingTime() {
        let typingTime = 0;
        let totalWord = 0;
        for (let i = 0; i < this.results.length; i++) {
          if (!this.results[i].forgot) {
            typingTime += this.results[i].typingTime;
            totalWord += 1
          }
        }
        return typingTime / totalWord
      },
    },
    methods: {
      keyHit(e) {
        if (e.code === 'Enter' && !this.gameStart) {
          if (this.$refs.saveGameResults) {
            this.$refs.saveGameResults.click()
          }
        }
      },
      saveResults(watchTime, startTypeTime, typingTime, correct, word, typedWord, forgot) {
        this.results.push({
          'word': word,
          'watchTime': watchTime,
          'startTypeTime': startTypeTime,
          'typingTime': typingTime,
          'correct': correct,
          'typedWord': typedWord,
          'forgot': forgot,
          'totalTime': watchTime + startTypeTime + typingTime
        });
        console.log('end');
        this.wordInRound++;
        if (this.wordInRound === this.words.length) {

          this.round++;
          this.wordInRound = 0;
          this.gameStart = false;
        }
        this.wordInd++;
        if (!(this.wordInd in this.words)) {
          this.wordInd = 0
        }
        this.word = this.words[this.wordInd];
      },
      startGame() {
        this.results = [];
        this.gameStart = true;
      }
    }
  }
</script>
<style>
  .wrong-letter {
    color: red;
  }
</style>



